import type { CSSProperties, ReactEventHandler } from "react";
import React, { forwardRef, useEffect, useRef, useState } from "react";
import { Logger } from "./logger.js";

// Message types for communication between parent and child
export enum MessageType {
  RESIZE = "civic-iframe-resize",
  READY = "civic-iframe-ready",
}

export interface ResizeMessage {
  type: MessageType.RESIZE;
  height: number;
  width?: number; // Optional - we can add width resizing support later if needed
}

export interface ReadyMessage {
  type: MessageType.READY;
}

export type IframeMessage = ResizeMessage | ReadyMessage;

// Props for the IframeResizer component
export interface IframeResizerProps {
  /**
   * Initial height of the iframe (default: 100px)
   */
  initialHeight?: string;

  /**
   * Whether to animate height changes (default: true)
   */
  animate?: boolean;

  /**
   * Duration of height change animation in ms (default: 250ms)
   */
  animationDuration?: number;

  /**
   * Function called when iframe height changes
   */
  onResizeHeight?: (height: number) => void;

  /**
   * Whether to check the origin of messages (default: false)
   */
  checkOrigin?: boolean | string[];

  /**
   * Allowed origins if checkOrigin is true
   */
  allowedOrigins?: string[];

  /**
   * Enable debug mode for detailed logging
   */
  debug?: boolean;

  /**
   * Standard iframe props
   */
  src?: string;
  id?: string;
  title?: string;
  allow?: string;
  allowFullScreen?: boolean;
  name?: string;
  onLoad?: ReactEventHandler<HTMLIFrameElement>;
  style?: CSSProperties;
  className?: string;
  "data-testid"?: string;
}

export const IframeResizer = forwardRef<HTMLIFrameElement, IframeResizerProps>(
  (
    {
      initialHeight = "100px",
      animate = true,
      animationDuration = 250,
      onResizeHeight,
      checkOrigin = false,
      allowedOrigins = [],
      debug = false,
      style = {},
      ...iframeProps
    },
    ref,
  ) => {
    const internalRef = useRef<HTMLIFrameElement>(null);
    const [height, setHeight] = useState<string>(initialHeight);

    // Create logger
    const logger = useRef(
      new Logger({
        debug,
        prefix: "[CivicIframeResizer][Parent]",
      }),
    ).current;

    // Forward ref to parent component
    useEffect(() => {
      if (ref && internalRef.current) {
        if (typeof ref === "function") {
          ref(internalRef.current);
        } else {
          // Using a mutable object as a workaround for the read-only property
          const refCopy = ref as { current: HTMLIFrameElement | null };
          refCopy.current = internalRef.current;
        }
      }
    }, [ref]);

    const transitionStyle = animate ? { transition: `all ${animationDuration}ms ease` } : {};

    // Request size from child iframe - wrapped in useCallback to avoid dependency issues
    const requestSizeFromChild = React.useCallback(() => {
      if (internalRef.current && internalRef.current.contentWindow) {
        try {
          logger.debug("Requesting size from child iframe");
          internalRef.current.contentWindow.postMessage({ type: "request-size" }, "*");
        } catch (err) {
          logger.error("Error requesting size:", err);
        }
      }
    }, [logger]);

    // Handle iframe load event - request size after the iframe has loaded
    const handleIframeLoad = (e: React.SyntheticEvent<HTMLIFrameElement, Event>) => {
      logger.debug("Iframe loaded, src:", internalRef.current?.src);

      // Call original onLoad handler if provided
      if (iframeProps.onLoad) {
        iframeProps.onLoad(e);
      }

      // Schedule multiple size requests after load
      setTimeout(requestSizeFromChild, 100);
      setTimeout(requestSizeFromChild, 500);
      setTimeout(requestSizeFromChild, 1000);
    };

    // Handle messages from the child iframe
    useEffect(() => {
      const handleMessage = (event: MessageEvent) => {
        // Log all messages for debugging
        logger.debug("Received message:", event.data, "from origin:", event.origin);

        // Skip processing if not from our iframe
        if (!internalRef.current) {
          logger.debug("No iframe reference available");
          return;
        }

        // Check if the message is related to our iframe
        try {
          const message = event.data as IframeMessage;

          // Handle ready message
          if (message.type === MessageType.READY) {
            logger.debug("Received READY message from child iframe");
            // Request size after receiving ready message
            setTimeout(requestSizeFromChild, 100);
          }

          // Handle resize message
          if (message.type === MessageType.RESIZE) {
            logger.debug("Received RESIZE message, height:", message.height);
            setHeight(`${message.height}px`);

            // Also set height directly on the iframe element as a safety measure
            if (internalRef.current) {
              internalRef.current.style.height = `${message.height}px`;
            }

            onResizeHeight?.(message.height);
          }
        } catch (error) {
          // Log error with message data
          logger.error("Error processing message:", error, "Message data:", event.data);
        }
      };

      window.addEventListener("message", handleMessage);

      return () => {
        window.removeEventListener("message", handleMessage);
      };
    }, [checkOrigin, allowedOrigins, onResizeHeight, logger, requestSizeFromChild]);

    // Use the onLoad prop to handle iframe loading
    useEffect(() => {
      // Request size periodically during the first 10 seconds to ensure we get the correct height
      const initialLoadIntervals = [100, 500, 1000, 2000, 3000, 5000, 7000, 10000];
      const timeouts: ReturnType<typeof setTimeout>[] = [];

      // Schedule size requests at specified intervals
      initialLoadIntervals.forEach((interval) => {
        const timeoutId = setTimeout(requestSizeFromChild, interval);
        timeouts.push(timeoutId);
      });

      return () => {
        timeouts.forEach((timeoutId) => clearTimeout(timeoutId));
      };
    }, [requestSizeFromChild]);

    return (
      <iframe
        {...iframeProps}
        onLoad={handleIframeLoad}
        ref={internalRef}
        style={{
          border: "none",
          width: "100%",
          overflow: "hidden",
          height,
          minHeight: "200px", // Ensure a minimum height to prevent empty iframe
          ...transitionStyle,
          ...style,
        }}
      />
    );
  },
);

IframeResizer.displayName = "IframeResizer";
