"use client";

import React, { createContext, useContext, useState } from "react";
import { AuthStatus } from "@/types.js";

interface AuthStatusContextType {
  authStatus: AuthStatus;
  setAuthStatus: React.Dispatch<React.SetStateAction<AuthStatus>>;
}

const AuthStatusContext = createContext<AuthStatusContextType | undefined>(
  undefined,
);

export const AuthStatusProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const [authStatus, setAuthStatus] = useState<AuthStatus>(
    AuthStatus.UNAUTHENTICATED,
  );

  return (
    <AuthStatusContext.Provider value={{ authStatus, setAuthStatus }}>
      {children}
    </AuthStatusContext.Provider>
  );
};

export const useAuthStatus = (): AuthStatusContextType => {
  const context = useContext(AuthStatusContext);
  if (context === undefined) {
    throw new Error("useAuthStatus must be used within an AuthStatusProvider");
  }
  return context;
};
