/**
 * Logger utility for iframe-resizer
 */

export enum LogLevel {
  ERROR = 0,
  WARN = 1,
  INFO = 2,
  DEBUG = 3,
}

export interface LoggerOptions {
  /**
   * Minimum log level to output
   */
  level?: LogLevel;

  /**
   * Whether to enable debug mode
   */
  debug?: boolean;

  /**
   * Prefix for log messages
   */
  prefix?: string;
}

/**
 * Logger class for iframe-resizer
 */
export class Logger {
  private level: LogLevel;
  private prefix: string;

  constructor(options: LoggerOptions = {}) {
    this.level = options.debug ? LogLevel.DEBUG : options.level || LogLevel.INFO;
    this.prefix = options.prefix || "[CivicIframeResizer]";
  }

  /**
   * Log error message
   */
  error(message: string, ...args: unknown[]): void {
    if (this.level >= LogLevel.ERROR) {
      console.error(`${this.prefix} ${message}`, ...args);
    }
  }

  /**
   * Log warning message
   */
  warn(message: string, ...args: unknown[]): void {
    if (this.level >= LogLevel.WARN) {
      console.warn(`${this.prefix} ${message}`, ...args);
    }
  }

  /**
   * Log info message
   */
  info(message: string, ...args: unknown[]): void {
    if (this.level >= LogLevel.INFO) {
      console.info(`${this.prefix} ${message}`, ...args);
    }
  }

  /**
   * Log debug message - only shown in debug mode
   */
  debug(message: string, ...args: unknown[]): void {
    if (this.level >= LogLevel.DEBUG) {
      console.debug(`${this.prefix} ${message}`, ...args);
    }
  }

  /**
   * Set the log level
   */
  setLevel(level: LogLevel): void {
    this.level = level;
  }

  /**
   * Enable debug mode
   */
  enableDebug(): void {
    this.level = LogLevel.DEBUG;
  }

  /**
   * Disable debug mode
   */
  disableDebug(): void {
    this.level = LogLevel.INFO;
  }
}

/**
 * Create a default logger
 */
export const createLogger = (options: LoggerOptions = {}): Logger => {
  return new Logger(options);
};
